.result-name-link {
	color: #e7e9f0;
	text-decoration: none;
}

.result-name-card {
	background: #282d4f;
	display: flex;
	justify-content: center;
	padding: 10px 20px;
	min-width: 75px;
	border-radius: 10px;
	-webkit-transition: background 0.35s; /* Safari */
	transition: background 0.35s, min-width 1s;
	box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.1);
	margin: 10px 15px;
}

.result-name-card:hover {
	background: #4a5079;
	box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.2);
}

.result-name {
	margin: 0;
	font-weight: 100;
}
